import { Layout, Menu, type MenuProps } from 'antd';
import {
  AppstoreAddOutlined,
  BookOutlined,
  CreditCardOutlined,
  DashboardOutlined,
  QuestionCircleOutlined,
  SettingOutlined,
} from '@ant-design/icons';
import {
  type CSSProperties,
  type PropsWithChildren,
  useEffect,
  useState,
} from 'react';
import { useNavigate, useLocation } from 'react-router';
import logoUrl from '../../assets/logo-white.svg';

const { Sider } = Layout;

type MenuItem = Required<MenuProps>['items'][number];

const items: MenuItem[] = [
  {
    key: '/',
    label: 'Dashboard',
    icon: <DashboardOutlined />,
  },
  {
    key: '/applications',
    label: 'Applications',
    icon: <AppstoreAddOutlined />,
  },
  {
    type: 'divider',
  },
  {
    key: '/documentation',
    label: 'Documentation',
    icon: <BookOutlined />,
  },
  {
    key: '/help',
    label: 'Help',
    icon: <QuestionCircleOutlined />,
  },
  {
    type: 'divider',
  },
  {
    key: '/billing',
    label: 'Billing',
    icon: <CreditCardOutlined />,
  },
  {
    key: '/settings',
    label: 'Settings',
    icon: <SettingOutlined />,
  },
];

const SideBar = ({ children }: PropsWithChildren) => {
  const [selectedPath, setPath] = useState<string>('dashboard');
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (location.pathname.includes('applications')) {
      setPath('/applications');
    } else {
      setPath(location.pathname);
    }
  }, [location]);

  const onMenuClick = ({ key }: { key: string }) => {
    navigate(`${key}`);
  };

  return (
    <Layout hasSider>
      <Sider style={siderStyle}>
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            paddingTop: '20px',
          }}
        >
          <img src={logoUrl} style={{ width: 150 }} />
        </div>
        <Menu
          style={{ marginTop: 30 }}
          theme="dark"
          mode="inline"
          defaultSelectedKeys={['dashboard']}
          selectedKeys={[selectedPath]}
          items={items}
          onClick={onMenuClick}
        />
      </Sider>
      <Layout style={{ marginInlineStart: 250 }}>{children}</Layout>
    </Layout>
  );
};

const siderStyle: CSSProperties = {
  overflow: 'auto',
  height: '100vh',
  position: 'fixed',
  insetInlineStart: 0,
  top: 0,
  bottom: 0,
  scrollbarWidth: 'thin',
  scrollbarGutter: 'stable',
};

export default SideBar;
